.header {
  position: absolute;
  width: 100%;
}

.hero {
  background-image: url('./hero.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 115%;
  background-size: 50%;
}

.below-fold {
  background-color: hsla(0, 0%, 75%, 0.35);
}

.cyan-btn {
  background-color: hsl(180, 66%, 49%);
  color: white;
  font-weight: 700;
  outline: none;
  border-color: hsl(180, 66%, 49%);
  border-style: solid;
  transition: 0.2s;
}

.cyan-btn:hover {
  opacity: 0.85;
}

@media screen and (max-width: 767px) {
  .hero {
    background-size: 0 0;
  }
}
