@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  font-size: 18rem;
  font-weight: normal;
  line-height: 1.2;
  font-family: "Poppins", sans-serif;
  max-width: 100%;
  margin: 0;
  padding: 0;
  color: hsl(0, 0%, 75%);
}

.c-padding {
  padding-left: 100rem;
  padding-right: 100rem;
}

img {
  max-width: 100%;
  height: auto;
}

@media screen and (min-width: 1400px) {
  .c-padding {
    padding-left: 200rem;
    padding-right: 200rem;
  }
}

@media screen and (max-width: 991px) {
  .c-padding {
    padding-left: 60rem;
    padding-right: 60rem;
  }
}

@media screen and (max-width: 767px) {
  .c-padding {
    padding-left: 30rem;
    padding-right: 30rem;
  }
}
@media screen and (max-width: 580px) {
  .c-padding {
    padding-left: 15rem;
    padding-right: 15rem;
  }
}
.header {
  position: absolute;
  width: 100%;
}

.hero {
  background-image: url(/shortly/static/media/hero.5029d049.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 115%;
  background-size: 50%;
}

.below-fold {
  background-color: hsla(0, 0%, 75%, 0.35);
}

.cyan-btn {
  background-color: hsl(180, 66%, 49%);
  color: white;
  font-weight: 700;
  outline: none;
  border-color: hsl(180, 66%, 49%);
  border-style: solid;
  transition: 0.2s;
}

.cyan-btn:hover {
  opacity: 0.85;
}

@media screen and (max-width: 767px) {
  .hero {
    background-size: 0 0;
  }
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40rem 0;
}

.left {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.left,
.right {
  /* flex-basis: 50%; */
  flex-shrink: 1;
}

.left > .menu {
  padding: 0 20rem;
  max-width: 100%;
}

.menu > .item {
  padding: 0 20rem;
  font-size: 16rem;
  font-weight: 600;
  transition: 0.2s;
  color: hsl(0, 0%, 75%);
  cursor: pointer;
}

.menu > .item:hover {
  color: hsl(257, 27%, 26%);
}

.right > .menu > .item {
  padding: 10rem 20rem;
  border-radius: 50rem;
}

.right > .menu > .item:hover {
  background-color: hsl(180, 66%, 49%);
  color: white;
}

.mobile-menu {
  display: none;
}

@media screen and (max-width: 991px) {
  .menu > .item {
    padding: 0 15rem;
    font-size: 14rem;
  }
  .right > .menu > .item {
    padding: 6rem 18rem;
  }
}

@media screen and (max-width: 767px) {
  .menu {
    display: none;
  }
  .mobile-menu {
    display: inline-block;
  }
  .logo {
    max-width: 100rem;
  }
  .menu > .item {
    padding: 0 15rem;
    font-size: 14rem;
  }
  .right > .menu > .item {
    padding: 6rem 18rem;
  }
}

.hero-wrapper {
  min-height: 120vh;
  display: flex;
  align-items: center;
}

.hero-text {
  font-size: 82rem;
  font-weight: 700;
  color: hsl(260, 8%, 14%);
}

.hero-desc {
  font-size: 22rem;
  font-weight: 500;
  margin-top: 10rem;
}

.hero-left {
  max-width: 620rem;
}

.action-btn {
  background-color: hsl(180, 66%, 49%);
  margin-top: 30rem;
  padding: 15rem 40rem;
  border-radius: 50rem;
  color: white;
  font-weight: 700;
  font-size: 22rem;
}

.hero-img {
  display: none;
}

@media screen and (max-width: 991px) {
    .hero-text {
        font-size: 48rem;
    }
    .hero-left {
        max-width: 500rem;
    }
}

@media screen and (max-width: 767px) {
  .hero-wrapper {
    flex-direction: column;
    margin-bottom: 150rem;
    min-height: auto;
  }
  .hero-img {
    display: inline-block;
    margin-top: 20%;
  }
  .hero-left {
    text-align: center;
    max-width: 500rem;
    margin: 60rem auto;
  }
  .hero-text {
    font-size: 40rem;
  }
  .hero-desc {
    font-size: 18rem;
  }
  .action-btn {
    padding: 10rem 30rem;
    font-size: 18rem;
  }
}
@media screen and (max-width: 580px) {
}

.violet-area {
  background-color: hsl(257, 27%, 26%);
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-image: url(/shortly/static/media/bg-shorten-desktop.a82de60e.svg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40rem 80rem;
  margin-bottom: 20rem;
}

.form {
  display: flex;
  margin-top: 20rem;
}

.input {
  display: flex;
  flex-direction: column;
  margin: 0 10rem;
}

.input:nth-child(1) {
  flex-basis: 85%;
}

.input > input {
  padding: 15rem 30rem;
  border-radius: 5rem;
  font-size: 20rem;
  outline: none;
  border-style: solid;
}

.input.mb > input {
  border: 2rem solid #dbdbdb;
}

input[type="submit"] {
  cursor: pointer;
  padding: 15rem 60rem;
}

.red::-webkit-input-placeholder {
  color: hsl(0, 87%, 67%);
  opacity: 1;
}

.red:-ms-input-placeholder {
  color: hsl(0, 87%, 67%);
  opacity: 1;
}

.red::-ms-input-placeholder {
  color: hsl(0, 87%, 67%);
  opacity: 1;
}

.red::placeholder {
  color: hsl(0, 87%, 67%);
  opacity: 1;
}

.red:-ms-input-placeholder {
  color: hsl(0, 87%, 67%);
}

.red::-ms-input-placeholder {
  color: hsl(0, 87%, 67%);
}

.red {
  border-color: hsl(0, 87%, 67%)!important;
  color: hsl(0, 87%, 67%);
}

.warning {
  color: hsl(0, 87%, 67%);
  margin-top: 5rem;
  font-style: italic;
  font-size: 16rem;
  display: none;
}

.warning.show {
  display: inline-block;
}

.white-area {
  padding: 20rem 40rem;
  background: white;
  margin: 20rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  -webkit-transform: translateY(-110%);
          transform: translateY(-110%);
  /* display: none; */
}

.main-link {
  color: hsl(257, 27%, 26%);
}

.short-link {
  display: flex;
  align-items: center;
}

.short-link > .link {
  color: hsl(180, 66%, 49%);
  margin: 0 30rem;
}

.copy-btn {
  padding: 10rem 20rem;
  border-radius: 5rem;
  transition: 0.2;
  min-width: 90rem;
}

.copy-btn:hover {
  opacity: 0.8;
}

.copied {
  background-color: hsl(257, 27%, 26%);
}

.highlight-heading {
  -webkit-transform: translateY(-25%);
          transform: translateY(-25%);
  margin: 60rem auto;
  max-width: 500rem;
  text-align: center;
  line-height: 1.6;
}

.highlight-heading > .heading {
  font-size: 40rem;
  font-weight: 700;
  color: hsl(257, 27%, 26%);
  margin-bottom: 10rem;
}

.three-points {
  padding-bottom: 40rem;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.three-points::before {
  content: "";
  height: 10rem;
  width: 90%;
  background-color: hsl(180, 66%, 49%);
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.point {
  width: 31.5%;
  background: white;
  z-index: 2;
  padding-left: 30rem;
  padding-right: 30rem;
  box-shadow: 0 0 10rem -10rem rgba(0, 0, 0, 0.25);
  border-radius: 5rem;
}

.point > .image {
  background: hsl(257, 27%, 26%);
  border-radius: 50%;
  display: flex;
  height: 85rem;
  width: 85rem;
  justify-content: center;
  align-items: center;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  box-shadow: 0 3rem 5rem 0rem rgba(0, 0, 0, 0.25);
}

.point > .title {
  font-weight: 700;
  margin-bottom: 20rem;
  color: hsl(257, 27%, 26%);
  font-size: 20rem;
}

.point > .content {
  font-size: 16rem;
  font-weight: 500;
  line-height: 1.4;
  margin-bottom: 60rem;
}

.point:nth-child(2) {
  -webkit-transform: translateY(15%);
          transform: translateY(15%);
}

.point:nth-child(3) {
  -webkit-transform: translateY(30%);
          transform: translateY(30%);
}

input[type="submit"] {
  cursor: pointer;
  min-width: 250rem;
  max-width: 100%;
}

input[type="submit"]:disabled {
  background-color: lightgrey;
  border-color: lightgrey;
}

.hidden {
  display: none;
}

@media screen and (max-width: 767px) {
  .form,
  .white-area,
  .short-link,
  .three-points {
    flex-direction: column;
  }
  .form {
    margin-top: 0;
  }
  .white-area {
    -webkit-transform: translateY(-70%);
            transform: translateY(-70%);
  }
  .short-link,
  .link {
    padding: 10rem 0;
  }
  .violet-area {
    padding: 30rem 60rem;
  }
  .point {
    width: 100%;
    text-align: center;
    margin-bottom: 60rem;
  }
  .three-points::before {
    width: 10rem;
    height: 90%;
  }
  .point > .image {
    margin: 0 auto;
  }
  .input.mb {
    margin-bottom: 20rem;
  }
}

@media screen and (max-width: 580px) {
  .violet-area {
    padding: 30rem;
  }
  .white-area {
    -webkit-transform: translateY(-55%);
            transform: translateY(-55%);
  }
  .wrapper {
    padding: 30rem 0;
  }
  .hero-text {
    font-size: 28rem;
  }
  .hero-desc {
    font-size: 16rem;
  }
  .action-area {
    margin-top: 50rem;
  }
  .highlight-heading > .heading {
    font-size: 28rem;
  }
  .highlight-heading {
    margin: 30rem auto;
    -webkit-transform: translateY(-40%);
            transform: translateY(-40%);
  }
  .highlight-heading > .desc {
    font-size: 16rem;
  }
  .input > input {
    padding: 10rem 20rem;
    font-size: 18rem;
  }
}

.action-area {
  background-color: hsl(257, 27%, 26%);
  background-image: url(/shortly/static/media/bg-boost-desktop.dc8b886b.svg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 60rem 20rem;
  margin-top: 200rem;
  text-align: center;
}


.action-area > .heading {
    font-size: 40rem;
    font-weight: 700;
    color: white;
}

@media screen and (max-width: 580px) {
    .action-area {
      margin-top: 50rem;
    }
    .action-area > .heading {
        font-size: 28rem;
    }
  }
